import React ,{Component}from 'react'
import {Avatar, Button, FontIcon, TextField, DialogContainer} from 'react-md';
import {ToastsContainer, ToastsStore} from 'react-toasts';
import firebaseApp from '../firebase/config'
import firebase from 'firebase'
import './profilesettings.css'

class ProfileSettings extends Component {

    constructor(){
        super()

        // Some of this states are used to manage user details and others  input box details

        this.state={
            userEmail:'',
            userImgUrl:'',
            userName:"",
            newUserName:"",
            userIconFirstLetter:'',
            newUserImgUrl:"",
            emailVerified:'',
            currentPassword:'',
            newUserPassword:'',
            currentPasswordForDelete:'',
            visible: false,
            currentPasswordForDeleteError:false,
            currentPasswordForDeleteErrorText:'',
            emailVerifiedIcon:'',
            emailVerifiedTittle:'',
            buttonVerifycheck:true,
            newPasswordErrorForTextFiel:false,
            newPasswordErrorTextForTextFiel:'',
            currentPasswordErrorTextForTextFiel:'',
            currentPasswordErrorForTextFiel:false,
        }

        //this helps to get firebase auth called
        this.auth = firebaseApp.auth()

        //this checks current user
        this.user = firebaseApp.auth().currentUser

        //this helps debugging some functions
        this.handleSpanSetting = this.handleSpanSetting.bind(this)
        this.handleNewUserName = this.handleNewUserName.bind(this)
        this.handleNewUserImgUrl = this.handleNewUserImgUrl.bind(this)
        this.saveChanges = this.saveChanges.bind(this)
    }


    componentDidMount(){
        //this fetchs user details from database
        this.name = this.user.displayName
        this.email = this.user.email
        this.uid = this.user.uid
        this.emailVerified = this.user.emailVerified;
        this.photoURL = this.user.photoURL
        //this gets the first letter from the username to update it in user icon
        this.userIconFirstLetter =  this.name.toString().charAt(0).toUpperCase()
        // this updates states with all user infos
        this.setState({
            userEmail: this.email,
            userName: this.name,
            userIconFirstLetter: this.userIconFirstLetter,
            userImgUrl: this.photoURL,
            newUserName: this.name,
            newUserImgUrl: this.photoURL,
            emailVerified: this.emailVerified,
          })
          if(this.emailVerified === false){
              //this detects if user email is verified or no if not it will post an err sign in his edit settings
              this.setState({
                emailVerifiedIcon:'report',
                emailVerifiedTittle:'Email is not Verified',
                buttonVerifycheck:false,
              })
          }else{
            this.setState({
                //this detects if user email is verified or no if not it will post a done sign in his edit settings
                emailVerifiedIcon:'done',
                emailVerifiedTittle:'Email is Verified',
                buttonVerifycheck:true,
              })
          }
          // Prompt the user to re-provide their sign-in credentials

    }

    //this passes a function called handleSpanSetting() to homepage so it can hide profile settings
    handleSpanSetting(){
        this.props.handleSpanSetting()
    }

    //this updates newUserName state with the variable that the input gives on change
    handleNewUserName(newUserNameVal){
        this.setState({
            newUserName: newUserNameVal
        })
    }

    //this updates newUserImgUrl state with the variable that the input gives on change
    handleNewUserImgUrl(newUserImgVal){
        this.setState({
            newUserImgUrl: newUserImgVal
        })
    }


    saveChanges(){

            //this checks if user name input box and currentpassword is empty or not
            if(this.state.newUserName !== ''  && this.state.currentPassword !== ''){
                //this checks if currentpassword is not empty and new user password is empty
                if(this.state.currentPassword !== '' && this.state.newUserPassword === '' ){
                    //this will send an err to user
                    ToastsStore.error('You need to fill the new password to change your password')
                    this.setState({
                        newPasswordErrorForTextFiel:true,
                        newPasswordErrorTextForTextFiel:'You need to fill the new password to change your password',
                        currentPasswordErrorTextForTextFiel:'',
                        currentPasswordErrorForTextFiel:false,
                    })
                }
                else{
                    //this checks if currentpassword input box variable = to the user password or not
                    //then it will update user password with the new one and send a sucess toaster and reload page

                    this.reauthenticate(this.state.currentPassword).then(() => {
                        this.user.updatePassword(this.state.newUserPassword).then(()=>{
                            ToastsStore.success('Update Was Successfull.Page will Auto reload in 3 seconds to update your account details.')
                        setTimeout(
                            () => {
                                window.location.reload();
                            },4500)
                        })
                    }).catch((error) =>{
                        ToastsStore.error(error.message)
                        if(error.message === 'The password is invalid'){
                            this.setState({
                                ewPasswordErrorForTextFiel:true,
                                newPasswordErrorTextForTextFiel:'The password that you inserted in Current Password input is invalid',
                                currentPasswordErrorTextForTextFiel:'The password that you inserted in Current Password input is invalid',
                                currentPasswordErrorForTextFiel:true,
                            })
                        }
                    })
                }
            }
            //this checks if newUserName is changed only if it is it will update user displayName and if
            // the user put a photo it will update it 
            else if(this.state.newUserName !== ''){
                this.user.updateProfile({
                    displayName: this.state.newUserName,
                    photoURL: this.state.newUserImgUrl
                  }).then(() =>{
                    ToastsStore.success('Update Was Successfull.Page will Auto reload in 3 seconds to update your account details.')
                    setTimeout(
                        () => {
                            window.location.reload();
                        },4500)
                  })
            }else{
                ToastsStore.error('UserName input needs to be filled')
            }
    }

    //this is used just to check if currentpassword is = to the original password
    reauthenticate = (currentPassword) =>{
        this.cred = firebase.auth.EmailAuthProvider.credential(this.user.email, currentPassword)
        return this.user.reauthenticateWithCredential(this.cred)
    }

    //this is used just to check if currentpassword is = to the original password
    reauthenticateForDelete = (currentPasswordForDelete) =>{
        this.cred = firebase.auth.EmailAuthProvider.credential(this.user.email, currentPasswordForDelete)
        return this.user.reauthenticateWithCredential(this.cred)
    }

    //this just sends email verification to the user
    verifyemail = () => {
        this.user.sendEmailVerification().then(() =>{
            ToastsStore.success("An email verification was sent to your email box")
        }).catch((err) =>{
            ToastsStore.error(err.message)
        })
    }

    //this manages the display of the delete button popup
    show = () => {
        this.setState({ visible: true });
      };
      
      hidebox = () => {
        this.setState({ 
            visible: false ,
            currentPasswordForDeleteError:false,
            currentPasswordForDeleteErrorText:'',
            currentPasswordForDelete:'',
        });
      }


       //this checks if currentPasswordForDelete input box variable = to the user password or not
        //then it will delete the account
      confirm = () => {
        this.reauthenticateForDelete(this.state.currentPasswordForDelete).then(() =>{
            this.user.delete().then(() =>{
                ToastsStore.success('Your account just got deleted.Page will Auto reload in 3 seconds to update your account details.')
            }).catch((error) =>{
                ToastsStore.error(error.message)
              });
        }).catch((err) => {
        if(err.message === 'The password is invalid or the user does not have a password.'){
            this.setState({
                currentPasswordForDeleteError:true,
                currentPasswordForDeleteErrorText:'The password is incorrect.',
            })
        }
        })
      }


      //this manages the currentPassword state by updating it with currentPasswordVal value
      handlecurrentPassword = (currentPasswordVal) =>{
        this.setState({
            currentPassword: currentPasswordVal
        })
      }

      //this manages the newUserPassword state by updating it with newUserPasswordVal value
      handlenewUserPassword = (newUserPasswordVal) =>{
        this.setState({
            newUserPassword: newUserPasswordVal
        })
      }

      //this manages the currentPasswordForDelete state by updating it with currentPasswordForDeleteVal value
      handlecurrentPasswordForDelete = (currentPasswordForDeleteVal) =>{
        this.setState({
            currentPasswordForDelete: currentPasswordForDeleteVal
        })
      }

    render(){

        //this is take from the library 'react-md' it just handles wat is inside of action array
        const actions = [];
            actions.push({ secondary: true, children: 'Cancel', onClick: this.hidebox });
            actions.push(<Button flat primary onClick={this.confirm}>Confirm</Button>);

        const divid = this.props.divid

        //this gets user icon
        const UserIcon = e =>{
            if(this.state.userImgUrl === null){
                
                return(<Avatar className="UserIconSettings">{this.state.userIconFirstLetter}</Avatar>)
            }else{
                return(<Avatar src={this.state.userImgUrl} role="presentation" className="UserIconSettings"/>)
            }
        };

        return(
        <div>
            <ToastsContainer store={ToastsStore} className="ToasterLeftTopSide"/>
            <div className="modal" id={divid}>
                <div className="modal-content">
                        <div className="modal-header">

                        <span className="close" onClick={this.handleSpanSetting}>
                            <Button floating>close</Button>
                        </span>
                        
                        <br/>
                        <center><h2>Edit Profile</h2></center>
                        </div>
                        <div className="modal-body">

                            <center>
                                <div>
                                        <UserIcon IconBackgroundColor={this.state.IconBackgroundColor}/>
                                </div>
                                <div className="divOfEmail">
                                    <div>{this.state.userEmail}</div>
                                    <div className='divOfEmailVerf'><FontIcon title={this.state.emailVerifiedTittle}>{this.state.emailVerifiedIcon}</FontIcon></div>
                                </div>
                            </center>

                        <br/>

                            <hr/>
                            <TextField
                                id="floating-center-title"
                                label="UserName"
                                lineDirection="center"
                                value={this.state.newUserName}
                                onChange={this.handleNewUserName}
                                className="md-cell md-cell--bottom inputbox"
                            />
                            <TextField
                                id="floating-center-title"
                                label="User Image Url"
                                lineDirection="center"
                                value={this.state.newUserImgUrl}
                                onChange={this.handleNewUserImgUrl}
                                className="md-cell md-cell--bottom inputbox"
                            />

                            <TextField
                                id="floating-center-title"
                                type='password'
                                label="Current Password"
                                lineDirection="center"
                                value={this.state.currentPassword}
                                onChange={this.handlecurrentPassword}
                                className="md-cell md-cell--bottom inputbox"
                                error={this.state.currentPasswordErrorForTextFiel}
                                errorText={this.state.currentPasswordErrorTextForTextFiel}
                            />

                            <TextField
                                id="floating-center-title"
                                type='password'
                                label="New Password"
                                lineDirection="center"
                                value={this.state.newUserPassword}
                                onChange={this.handlenewUserPassword}
                                className="md-cell md-cell--bottom inputbox"
                                error={this.state.newPasswordErrorForTextFiel}
                                errorText={this.state.newPasswordErrorTextForTextFiel}
                            />

                            <Button raised primary swapTheming disabled={this.state.buttonVerifycheck} title={this.state.emailVerifiedTittle}
                            className="verifyButton" 
                            onClick={this.verifyemail}>Verify My Email</Button>

                            <div className="DivContainDeleteButton">
                            <Button raised secondary swapTheming 
                            className="DeleteButton" 
                            onClick={this.show}>Delete Account</Button>
                            </div>

                            <DialogContainer
                            id="simple-action-dialog"
                            visible={this.state.visible}
                            onHide={this.hidebox}
                            actions={actions}
                            title="Do you readly want to delete your account?"
                            >
                                <TextField
                                id="floating-center-title"
                                type='password'
                                label="Current Password"
                                lineDirection="center"
                                value={this.state.currentPasswordForDelete}
                                onChange={this.handlecurrentPasswordForDelete}
                                className="md-cell md-cell--bottom inputbox"
                                error={this.state.currentPasswordForDeleteError}
                                errorText={this.state.currentPasswordForDeleteErrorText}
                            />
                            </DialogContainer>
                        </div>

                        <div className="modal-footer">
                            <center><Button raised primary swapTheming onClick={this.saveChanges}>Save Changes</Button></center>
                        </div>
                </div>

            </div>
        </div>
        )
    }

}

export default ProfileSettings