import React ,{Component}from 'react'
import ProfileSettings from './profilesettings'
import Navbar from './navbar'
import {Toolbar} from 'react-md';
import firebaseApp from '../firebase/config'
import {ToastsContainer, ToastsStore} from 'react-toasts';
import Loader from 'react-loader-spinner'
import './dashboard.css'


class Homepage extends Component{

    constructor(){
        super()
        this.state={
            loadingDash:false,
            showSettings:false,
        }

        // this is used to get the data from firebase related to loadingDash
        this.dataLoadingDash = firebaseApp.database().ref().child('loadingDash');

        this.auth = firebaseApp.auth()
        //this checks current user
        this.user = firebaseApp.auth().currentUser

        //this helps debugging some fanctions
        this.handleLogout = this.handleLogout.bind(this)
        this.handleSettings = this.handleSettings.bind(this)
        this.handleSpanSetting = this.handleSpanSetting.bind(this)
        

    }

    componentDidMount(){
        //this is the loading screen it fetchs data from firebase and updates with the new value time out used just to make it little bit longer 
        
        setTimeout(() => {this.dataLoadingDash.on('value' , snap => {
            this.setState({
            loadingDash: snap.val()
            })
          })},4500)
    }
    

    handleSettings(){
        //this handles the opening of the settings box

        this.setState({
            showSettings:true
        })
    }
    handleSpanSetting(){
        //this handles the closing of the settings box

        this.setState({
            showSettings:false
        })
    }

    handleLogout(){
        //this just signs out the user when the function is called

        this.auth.signOut()
    }

    render(){
        //this check if the web app is connected to the firebase or no if it isnt it will trigger a loading screen
       
        if(this.state.loadingDash === false){
            // this is the loading screen

            return(
                    <div className='LoadingScrean'>
                        <Loader type="Triangle" color="rgb(72, 219, 138)" height={80} width={80}/>
                    </div>
                );
            
        }
        // this gets triggered when the web app coonects to the firebase

        else{

            // this is the setting box it gets triggered when handleSettings() gets triggered

            const ProfileSettingsPopup = () => {
                if(this.state.showSettings === true){
                   return(
                        <ProfileSettings handleSpanSetting={this.handleSpanSetting} divid="myModal"/>
                   );
                }else{
                    return(null)
                }
            }

            return(
                
                <div>
                    <ToastsContainer store={ToastsStore} className="ToasterLeftTopSide"/>
                    <ProfileSettingsPopup/>
                    <Toolbar
                        themed
                        nav={<Navbar handleSettings={this.handleSettings} handleLogout={this.handleLogout}/>}
                        title="Social Media Project"
                    />
                </div>
            );
        }
    }


}

export default Homepage