import React ,{Component} from 'react'
import PropTypes from 'prop-types';
import {
    Avatar,
    FontIcon,
    AccessibleFakeButton,
    IconSeparator,
    DropdownMenu,
  } from 'react-md';
import firebaseApp from '../firebase/config'

class Navbar extends Component {

    constructor(){
        super()
        this.state={
            userImgUrl:'',
            userName:"",
            userIconFirstLetter:'',
        }

        this.auth = firebaseApp.auth()

        this.user = firebaseApp.auth().currentUser

        this.handleSettings = this.handleSettings.bind(this)
        this.handleLogout = this.handleLogout.bind(this)
    }

    componentDidMount(){
        this.auth.onAuthStateChanged((user) =>{
            if (user) {
                if (this.user != null) {

                    //this fetchs user details from firebase
                    this.name = this.user.displayName
                    this.email = this.user.email
                    this.uid = this.user.uid
                    this.photoURL = this.user.photoURL 
    
                    //this gets the first letter from the username to update it in user icon
                    this.userIconFirstLetter =  this.name.toString().charAt(0).toUpperCase()
        
                    this.setState({
                        userName: this.name,
                        userIconFirstLetter: this.userIconFirstLetter,
                        userImgUrl:this.photoURL,
                      })
                  }
              }
        })
    }

    handleSettings(){
        this.props.handleSettings()
    }

    handleLogout(){
        this.props.handleLogout()
    }

    render(){
        //this handles the user icon.Firstly it check if the user got an img or no.If not it will set it to default
            // it will check the user name and takes the first letter of it

            const UserIcon = e =>{
                if(this.state.userImgUrl === null){
                    return(<Avatar random className="UserIconLogo">{this.state.userIconFirstLetter}</Avatar>)
                }else{
                    return(<Avatar src={this.state.userImgUrl} role="presentation"/>)
                }
            };
            
            //this handles the user dropdown list.It is taken from the module react-md

            const AccountMenu = ({ simplifiedMenu }) => (
                <div>
                    <div className='UserIcon navBar'>
                        <DropdownMenu
                        id={`${!simplifiedMenu ? 'smart-' : ''}avatar-dropdown-menu`}
                        menuItems={[
                            'Preferences', 
                            <div 
                            role="button" 
                            className="md-fake-btn md-pointer--hover md-fake-btn--no-outline md-list-tile md-text Hovered" 
                            onClick={this.handleSettings}>Edit Profile</div>, 
                            { divider: true }, 
                            <div 
                            role="button" 
                            className="md-fake-btn md-pointer--hover md-fake-btn--no-outline md-list-tile md-text Hovered" 
                            onClick={this.handleLogout}>Log Out</div>,
                        ]}
                        anchor={{
                            x: DropdownMenu.HorizontalAnchors.CENTER,
                            y: DropdownMenu.VerticalAnchors.OVERLAP,
                        }}
                        position={DropdownMenu.Positions.TOP_RIGHT}
                        animationPosition="below"
                        sameWidth
                        simplifiedMenu={simplifiedMenu}
                        >
                        <AccessibleFakeButton
                            component={IconSeparator}
                            iconBefore
                            label={
                            <IconSeparator label={this.state.userName}>
                                <FontIcon>arrow_drop_down</FontIcon>
                            </IconSeparator>
                            }
                        >
                            <UserIcon/>
                        </AccessibleFakeButton>
                        </DropdownMenu>
                    </div>
                </div>
              );
              
              AccountMenu.propTypes = {
                simplifiedMenu: PropTypes.bool,
              };

              //^^this handles the user dropdown list.It is taken from the module react-md^^
        return(<AccountMenu className="md-btn--toolbar"/>)
    }
}

export default Navbar